
body {
	background-image: url(../../images/4-1.jpg);
	padding-top: 1.2rem;
}
.your_scores_container {
	width: 9.7rem;
	height: 9.1rem;
	background: url(../../images/4-2.png) no-repeat;
	background-size: 100% 100%;
	margin: 0 auto 0;
	position: relative;
}
.your_scores_container .your_scores {
	position: absolute;
	width: 100%;
	text-indent: 3.3rem;
	top: 4.7rem;
	font-size: 1.4rem;
	font-weight: 900;
	-webkit-text-stroke: 0.05rem #412318;
	font-family: 'Microsoft YaHei';
}
.your_scores_container .your_scores .score_num {
	font-family: Tahoma, Helvetica, Arial;
	color: #a51d31;
}
.your_scores_container .your_scores .fenshu {
	color: #a51d31;
}
.your_scores_container .result_tip {
	position: absolute;
	top: 7rem;
	width: 9rem;
	left: 0.6rem;
	color: #3e2415;
	font-size: 0.65rem;
	text-align: center;
}
.share_button {
	width: 6.025rem;
	height: 2.4rem;
	margin: 0.8rem auto 0;
	background: url(../../images/4-3.png) no-repeat 0.4rem 0;
	background-size: 5.825rem 100%;
}
.share_code {
	width: 5.3rem;
	margin: 1.5rem auto 0;
}
.share_code .share_header {
	color: #664718;
	font-size: 0.475rem;
	font-family: 'Microsoft YaHei';
	width: 7rem;
	font-weight: 500;
}
.share_code .code_img {
	height: 5.3rem;
	width: 5.3rem;
	margin-top: 0.5rem;
}
.share_cover {
	position: fixed;
	bottom: 0;
	right: 0;
	top: 0;
	left: 0;
	background: url(../../images/5-1.png) no-repeat;
	background-size: 100% 100%;
	opacity: 0.92;
}
.share_img {
	height: 10.975rem;
	width: 11.95rem;
	position: fixed;
	top: 0.5rem;
	left: 50%;
	margin-left: -5.975rem;
}



